body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f4f6f9;
  color: #333;
}

h2 {
  margin-bottom: 15px;
  color: #222;
}

.form-container {
  width: 350px;
  margin: 80px auto;
  padding: 25px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  text-align: center;
}

input {
  width: 90%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
}

button {
  background: #4CAF50;
  color: white;
  padding: 10px 18px;
  margin-top: 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background: #45a049;
}

.dashboard {
  max-width: 600px;
  margin: 60px auto;
  text-align: center;
}

.logout-btn {
  background: #e74c3c;
}

.logout-btn:hover {
  background: #c0392b;
}
